.about {
    padding-bottom: 0;
}

.about__grid {
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    gap: 3rem;
}

.about__img-wrapper {
    position: relative;
    padding-left: 1.5rem;
    z-index: -1;
}

.about__img-wrapper::before {
    content: '';
    position: absolute;
    bottom: -1.125rem;
    left: 0;
    background-color: var(--first-color);
    width: 85%;
    height: 100%;
    z-index: -1;
}

.about__img {
    width: 100%;
}

.about__details {
    margin-block: 1.25rem 2.25rem;
}

.about__details-description {
    display: flex;
    align-items: center;
}

.about__details,
.about__details-description {
    gap: 0.625rem;
}