.offer {
    background: url(../../assets/bg3.jpg) center / cover fixed;
    position: relative;
}

.offer::before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: var(--hsla);
}

.offer .section__title {
    position: relative;
    z-index: 1;
}

.offer__item {
    display: grid;
    grid-template-columns: 9fr 7fr;
    align-items: center;
}

.offer__img-wrapper {
    margin-right: -1rem;
}

.offer__content {
    background-color: var(--container-color);
    padding: 3.75rem 2.25rem;
    position: relative;
}

.offer__content::before {
    content: '';
    position: absolute;
    inset: 0.5rem;
    border: 2px dashed var(--first-color);
}

.offer__img,
.offer__content,
.offer__content::before {
    border-radius: var(--radius);
}

.offer__title {
    font-size: var(--h2-font-size);
    margin-bottom: 1.25rem;
}

.offer__discount {
    color: var(--title-color);
    font-family: var(--second-font);
    font-size: var(--large-font-size);
    font-weight: var(--weight-500);
}

.offer__description {
    margin-block: 0.5rem 1.5rem;
}

.swiper-pagination {
    position: initial;
    margin-top: 2rem;
}

.swiper-pagination-bullet {
    width: 16px;
    height: 16px;
    border: 3px solid var(--title-color);
    background-color: transparent;
    opacity: 1;
}

.swiper-pagination-bullet-ack {
    background-color: var(--first-color);
}